import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';

/**
 * Generated class for the Nivel_1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-nivel-6',
  templateUrl: 'aula9-n6.html',
})
export class Nivel6_aula9Page {

  checado: string;

  treinosList: any[] = [
    {id: 380, treino: "Ponto Lai Sao, Pai Kuen - 4x10", class: localStorage.getItem("380")},
    {id: 381, treino: "Treino Kiu Sao, Lai Sao no Brao + Fluncia - 4x10", class: localStorage.getItem("381")},
    {id: 382, treino: "Treino Kiu Sao, Lai Sao no pescoo + Fluncia - 4x10", class: localStorage.getItem("382")},
    {id: 383, treino: "Treino Fak Sao, Lai Sao + Fluncia - 4x10", class: localStorage.getItem("383")},
  ]; 

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private toast: ToastController
  ) {}

  ionViewDidLoad() {
  }

  salvar(id){
    if(this.treinosList[id -380].class !== "checkmark"){
      this.treinosList[id -380].class = "checkmark";

      this.checado = this.treinosList[id-380].class;

      localStorage.setItem(this.treinosList[id-380].id, this.treinosList[id-380].class);
      this.toast.create({ message: 'Alterao salva com sucesso', duration: 3000, position: 'botton' }).present();
    }else{
      this.treinosList[id -380].class = "";
      localStorage.setItem(this.treinosList[id-380].id, this.treinosList[id-380].class);

      this.checado = localStorage.getItem(id);
      this.toast.create({ message: 'Alterao salva com sucesso', duration: 3000, position: 'botton' }).present();
    }

    return this.treinosList; 
  }

}


