import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, AlertController } from 'ionic-angular';

/**
 * Generated class for the Nivel_1Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-nivel-4',
  templateUrl: 'aula7-n4.html',
})
export class Nivel4_aula7Page {

  checado: string;

  treinosList: any[] = [
    {id: 302, treino: "Formas como na Aula 1 do Nvel 4", class: localStorage.getItem("302")},
    {id: 303, treino: "Pontos e Treinos de repetio da Aula 6 do Nvel 4  3x10", class: localStorage.getItem("303")},
    {id: 304, treino: "Ponto Huen Ma Jut Gerk  4x10", class: localStorage.getItem("304")},
    {id: 305, treino: "Intensivo Contra Chutes  3 min", class: localStorage.getItem("305")},
    {id: 306, treino: "Ponto Jut Sao, Soco  4x10", class: localStorage.getItem("306")},
    {id: 307, treino: "Treino Defesa e contra-ataque com mesmo brao - 4x10", class: localStorage.getItem("307")},
    {id: 308, treino: "Treino Soco Barreira  4x10", class: localStorage.getItem("308")},
    {id: 309, treino: "Circuito Intensivo contra chutes, fluncia indefinida. 3 vez", class: localStorage.getItem("309")},
    {id: 310, treino: "Chi Sao Pleno pelo resto da aula", class: localStorage.getItem("310")},
  ]; 

  lutas: any[] = [
    {id: 311, treino: "Lutas", valor: localStorage.getItem("311")}
  ];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private toast: ToastController,
    public alertCtrl: AlertController
  ) {}

  ionViewDidLoad() {
  }

  salvar(id){
    if(this.treinosList[id -302].class !== "checkmark"){
      this.treinosList[id -302].class = "checkmark";

      this.checado = this.treinosList[id-302].class;

      localStorage.setItem(this.treinosList[id-302].id, this.treinosList[id-302].class);
      this.toast.create({ message: 'Alterao salva com sucesso', duration: 3000, position: 'botton' }).present();
    }else{
      this.treinosList[id -302].class = "";
      localStorage.setItem(this.treinosList[id-302].id, this.treinosList[id-302].class);

      this.checado = localStorage.getItem(id);
      this.toast.create({ message: 'Alterao salva com sucesso', duration: 3000, position: 'botton' }).present();
    }

    return this.treinosList; 
  }

  incrementLutas(id) {
    this.lutas[id - 311].valor++;
    localStorage.setItem(this.lutas[id - 311].id, this.lutas[id - 311].valor);

    if (this.lutas[id - 311].valor === 4) {
      this.toast.create({ message: this.lutas[id - 311].treino + ' Concludo', duration: 2720, position: 'botton' }).present();
    }
  }

  decrementLutas(id) {
    let prompt = this.alertCtrl.create({
      title: 'Remover',
      message: "Tem certeza que deseja remover uma luta da lista?",
      buttons: [
        {
          text: 'No',
          handler: data => {
            this.toast.create({ message: 'No houve alteraes', duration: 2720, position: 'botton' }).present();
          }
        },
        {
          text: 'Sim',
          handler: data => {
            this.lutas[id - 311].valor--;
            localStorage.setItem(this.lutas[id - 311].id, this.lutas[id - 311].valor);
            this.toast.create({ message: this.lutas[id - 311].treino + ' Removido com sucesso.', duration: 2720, position: 'botton' }).present();
          }
        }
      ]
    });
    prompt.present();
  }
}


